.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

/* general */
.center {
    text-align: center;
}

.right {
    text-align: right;
}

.btn {
    height: 20px;
}

ul {
    padding-inline-start: 0;
}

li {
    list-style-type: none;
}

.padding {
    padding: 0 20px;
}

button.button {
    margin: 20px 0;
    width: 180px;
    height: 40px;
    border-radius: 20px;
}

/* house colors */
.BlackEagles {
    background: rgb(206, 54, 87);
}
.BlueLions {
    background: rgb(77, 95, 169);
}
.GoldenDeer {
    background: rgb(223, 192, 74);
}
.Faculty {
    background: rgb(176, 225, 176);
}

/* auth pages */
div.auth-input {
    margin: 10px 0;
}

/* new playthrough */
.new-playthrough-choice {
    opacity: 0.5;
}

.new-playthrough-choice-selected {
    opacity: 1;
}

img.banner-choice {
    width: 100%;
    border-radius: 25px 25px 0 0;
}

img.byleth-choice {
    width: 100%;
    background: rgb(209, 209, 209);
    border-radius: 25px;
}

button.BlackEagles {
    background: rgb(206, 54, 87);
}
button.BlueLions {
    background: rgb(77, 95, 169);
}
button.GoldenDeer {
    background: rgb(223, 192, 74);
}
button.Faculty {
    background: rgb(176, 225, 176);
}

/* roster */
.roster-row {
    padding: 20px;
    border-top: 1px solid black;
}

.roster-img {
    height: 80px;
    width: 80px;
    border-radius: 15%;
    background: rgb(216, 216, 216);
}

.roster-img.BlackEagles {
    background: rgb(206, 54, 87);
}
.roster-img.BlueLions {
    background: rgb(77, 95, 169);
}
.roster-img.GoldenDeer {
    background: rgb(223, 192, 74);
}
.roster-img.Faculty {
    background: rgb(176, 225, 176);
}

.roster-row-student p {
    margin: 0;
}

p.roster-name {
    margin-bottom: 4px;
}

.roster-name {
    font-weight: bold;
}

/* student */
.goal-row {
    padding: 4px 20px;
}

/* add student */

.add-student-selected .roster-img {
    background: rgb(216, 216, 216);
}
.add-student-selected.BlackEagles {
    background: rgb(206, 54, 87);
}
.add-student-selected.BlueLions {
    background: rgb(77, 95, 169);
}
.add-student-selected.GoldenDeer {
    background: rgb(223, 192, 74);
}
.add-student-selected.Faculty {
    background: rgb(176, 225, 176);
}
